import axios from "axios";
import { create } from "zustand";
import debounce from "../helpers/debounce";
import Home from "../pages/Home";

const homeStore = create((set) => ({
  coins: [],
  trending: [],
  query: "",
  searching: false,
  searched: false,

  setQuery: (e) => {
    set({ query: e.target.value });
    homeStore.getState().searchCoins();
  },

  searchCoins: debounce(async () => {
    set({ searching: true });
    const { query, trending } = homeStore.getState();
    if (query.length > 2) {
      const res = await axios.get(
        `https://api.coingecko.com/api/v3/search?query=${query}`
      );

      const coins = res.data.coins.map((coin) => {
        return {
          id: coin.id,
          name: coin.name,
          image: coin.large,
        };
      });
      set({ coins, searching:false, searched: true });
    } else {
      set({ coins: trending, searching:false, searched: false });
    }
    // console.log(res.data)
  }, 500),

  fetchCoins: async () => {
    const [TrendingBit, CurrentPrice] = await Promise.all([
      axios.get("https://api.coingecko.com/api/v3/search/trending"),
      axios.get("https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=inr"
      ),
    ]);

    const btcPrice = CurrentPrice.data.bitcoin.inr;

    const coins = TrendingBit.data.coins.map((coin,safas,afsdf) => {
      return {
        name: coin.item.name,
        image: coin.item.large,
        id: coin.item.id,
        priceBTC: coin.item.price_btc.toFixed(10),
        priceINR: (coin.item.price_btc * btcPrice).toFixed(10),
      };
    });

    set({ coins, trending: coins });
    // console.log(coins)
  },
}));

