import React, { useState } from "react";
import { Link } from "react-router-dom";
import Header from "../components/Header";
import ListItem from "../components/ListItem";
import homeStore from "../stores/homeStore";
import Spinner from "react-bootstrap/Spinner";
import classNames from "classnames";

export default function Home() {
  const [sss,setSss]=useState();
  const inputEvent=(e)=>{
    // console.log(e);
    const{name,value,id}=e.target;
    console.log(name,value,id)
    if(name==="sss"){
      setSss(value)
    }
  }
  const store = homeStore();
  // console.log(store.searched);

  React.useEffect(() => {
    if(store.trending.length === 0)store.fetchCoins();
  }, []);

  return (
    <div>
      <Header back={false} />
      <header className="home-search">
        <div className="width">
          <h2>Search for a coin</h2>
          {/* <input type={"text"} value={sss} onChange={inputEvent} name={"sss"} id="sdafsdf"/> */}

          <div className={classNames("home-search-input", {searching: store.searching})}>
            <input type="text" value={store.query} onChange={store.setQuery} />
            <div className="spinner-container">
              <div className="loading-spinner"></div>
            </div>
          </div>
        </div>
      </header>
      <div className="home-cryptos">
        <div className="width">

        <h2>{store.searched ? "Search Results" : "Trending coins"}</h2>
          <div className="home-cryptos-list">
            {store.coins.map((coin) => {
              return <ListItem key={coin.id} coin={coin} />;
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
